}

.homepage {
  background: eggshell;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Trebuchet MS";
  font-size: 80px;
  background: -webkit-radial-gradient(center, ellipse, rgba(30,87,153,0.1) 0%,rgba(125,185,232,0) 100%);
  background: radial-gradient(ellipse at center, rgba(30,87,153,0.1) 0%,rgba(125,185,232,0) 100%); ;
}

.homepage a {
  text-decoration: none;
  color: #626567;
}

.homepage-choice-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 850px;
  height: 50%;
}

.homepage-big {
  flex-grow: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: all linear 50ms;
  transition: all linear 50ms;
  cursor: pointer;
  border-top: solid #B3B6B7 1px;
  border-bottom: solid #B3B6B7 1px;
  color: #7B7D7D;
}

.homepage-big:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  border: rgb(255, 235, 119) solid 2px;
  border-radius: 8px;
  box-shadow: rgba(235, 213, 90, 0.3) 0px 0px 90px 12px;
}

.homepage-big-left {
  background: #FDFEFE;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border-left: solid #B3B6B7 1px;
}

.homepage-big-right {
  background: #F7F9F9;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-right: solid #B3B6B7 1px;
}

@media only screen and (max-width: 600px) {
  .homepage {
    font-size: 40px;
  }

  .homepage-choice-wrapper {
    flex-direction: column;
  }

  .homepage-big {
    width: 80%;
  }

  .homepage-big {
    border-left: solid #B3B6B7 1px;
    border-right: solid #B3B6B7 1px;
  }

  .homepage-big-left {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    border-bottom-left-radius: 0px;
    border-bottom: none;
    border-top: solid #B3B6B7 1px;
  }
  
  .homepage-big-right {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    border-top-right-radius: 0px;
    border-top: none;
    border-bottom: solid #B3B6B7 1px;
  }
}
.sky-sphere-center {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  -webkit-transform: translateY(8%);
          transform: translateY(8%);
}

.sky-sphere {
  border-radius: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 30px #9ecaed;
  -webkit-transition: -webkit-transform 300ms linear;
  transition: -webkit-transform 300ms linear;
  transition: transform 300ms linear;
  transition: transform 300ms linear, -webkit-transform 300ms linear;
}

.sky-sphere-hue {
  box-shadow: 0 0 80px #e5ff6f;
}
.stars {
  -webkit-animation: moveUpDown 100s linear infinite;
          animation: moveUpDown 100s linear infinite;
  width: 1px;
  height: 1px;
  background: transparent;
  position: absolute;
  top: 0;
  opacity: 0.7;
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
}
.stars::after {
  content: " ";
  position: absolute;
  top: 0;
  width: 1px;
  height: 1px;
  background: transparent;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}

@-webkit-keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 100px;
  }
}

@keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 100px;
  }
}
